import { mapState } from "vuex";
import { USER_API } from "@/api/client";

export default {
  data() {
    return {
      isLoading: true,
      finished: false,
      loading: false,
      refreshing: false,
      rewardsList: [],
      start: 0,
      limit: 20,
    };
  },
  computed: {
    ...mapState(["local"]),
    locale() {
      return this.local.locale;
    },
    isBitKeep() {
      return this.local.UA.isBitKeep;
    },
  },
  async created() {
    this.isLoading = false;
  },
  beforeMount() {
    this.isBitKeep &&
      BitKeepInvoke.onLoadReady(() => {
        BitKeepInvoke.setTitle(this.$t("blindboxInvite.rewardTitle"));
        BitKeepInvoke.setIconAction();
        this.$nextTick(() => {
          BitKeepInvoke.appMode((err, res) => {
            let body = document.getElementsByTagName("body")[0];
            if (res == 1) {
              body.setAttribute("class", "theme-dark");
            } else {
              body.setAttribute("class", "theme-light");
            }
          });
        });
      });
  },
  mounted() {
    this.getRewardsList();
  },
  methods: {
    async getRewardsList() {
      const { data, status } = await USER_API.activityDoneRewardList({
        start: this.start * this.limit,
        limit: this.limit,
      });
      if (status == 1) {
        this.isLoading = false;
        this.loading = true;
        return this.$toast(data);
      }
      var moreList = data;
      moreList && this.rewardsList.push(...moreList);
      this.isLoading = false;
      this.$toast.clear();
      this.loading = false;
      this.start++;
      if (this.rewardsList.length >= data.total_count) {
        this.finished = true;
      }
    },
    async onRefresh() {
      const { data, status } = await USER_API.activityDoneRewardList({
        start: 0,
        limit: this.limit,
      });
      if (status == 1) {
        this.refreshing = false;
        return this.$toast(data);
      }
      this.rewardsList = data.data;
      this.refreshing = false;
    },
  },
};
